import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import enTranslation from "../src/locales/en.json";
import grTranslation from "../src/locales/gr.json";

i18n
  .use(initReactI18next) //   i18n  React
  .init({
    resources: {
      en: {
        translation: enTranslation, //     
      },
      gr: {
        translation: grTranslation, //     
      },
    },
    lng: "en", //  
    fallbackLng: "en", //          
    interpolation: {
      escapeValue: false, //     HTML
    },
    react: {
      useSuspense: false, //   Suspense mode  React
    },
  });

export default i18n;
